import React from "react";
import styled from "styled-components";
import { Author } from "./Author";
import { IFeedBox } from "./FeedBox";

const Container = styled.div`
    position: relative;
    cursor: pointer;
    width: calc(25% - 24px);

    @media (max-width: 1440px) {
        width: calc(33% - 24px);
    }

    @media (max-width: 768px) {
        width: calc(50% - 24px);
    }

    @media (max-width: 600px) {
        width: 100%;
    }
`;

const BackgroundImage = styled.img`
    width: 100%;
    height: auto;
    border-radius: 8px;
`;

export const FeedBox: React.FunctionComponent<IFeedBox.IProps> = props => {
    const { content, author } = props;
    return (
        <Container onClick={props.onClick}>
            <BackgroundImage src={content.thumbnailUrl} alt={props.title} onLoad={() => window.dispatchEvent(new Event('resize'))
            } />
            <Author
                vendorType={props.vendorType}
                postedAt={props.postedAt}
                name={author.name}
                profileImgUrl={author.profileImgUrl}
            />
        </Container>
    );
};
